                      data-name="Icon awesome-city"
                      d="M17.45,5.439H13.6V.68a.68.68,0,0,0-.68-.68H8.838a.68.68,0,0,0-.68.68V2.72H6.345V.453A.453.453,0,0,0,5.892,0H5.439a.453.453,0,0,0-.453.453V2.72H3.173V.453A.453.453,0,0,0,2.719,0H2.266a.453.453,0,0,0-.453.453V2.72H.68A.68.68,0,0,0,0,3.4V13.6a.907.907,0,0,0,.906.906H17.223a.907.907,0,0,0,.907-.906V6.119A.68.68,0,0,0,17.45,5.439ZM3.626,11.445a.34.34,0,0,1-.34.34H2.153a.34.34,0,0,1-.34-.34V10.311a.34.34,0,0,1,.34-.34H3.286a.34.34,0,0,1,.34.34Zm0-2.72a.34.34,0,0,1-.34.34H2.153a.34.34,0,0,1-.34-.34V7.592a.34.34,0,0,1,.34-.34H3.286a.34.34,0,0,1,.34.34Zm0-2.719a.34.34,0,0,1-.34.34H2.153a.34.34,0,0,1-.34-.34V4.872a.34.34,0,0,1,.34-.34H3.286a.34.34,0,0,1,.34.34Zm3.626,5.439a.34.34,0,0,1-.34.34H5.779a.34.34,0,0,1-.34-.34V10.311a.34.34,0,0,1,.34-.34H6.912a.34.34,0,0,1,.34.34Zm0-2.72a.34.34,0,0,1-.34.34H5.779a.34.34,0,0,1-.34-.34V7.592a.34.34,0,0,1,.34-.34H6.912a.34.34,0,0,1,.34.34Zm0-2.719a.34.34,0,0,1-.34.34H5.779a.34.34,0,0,1-.34-.34V4.872a.34.34,0,0,1,.34-.34H6.912a.34.34,0,0,1,.34.34Zm4.532,2.719a.34.34,0,0,1-.34.34H10.311a.34.34,0,0,1-.34-.34V7.592a.34.34,0,0,1,.34-.34h1.133a.34.34,0,0,1,.34.34Zm0-2.719a.34.34,0,0,1-.34.34H10.311a.34.34,0,0,1-.34-.34V4.872a.34.34,0,0,1,.34-.34h1.133a.34.34,0,0,1,.34.34Zm0-2.72a.34.34,0,0,1-.34.34H10.311a.34.34,0,0,1-.34-.34V2.153a.34.34,0,0,1,.34-.34h1.133a.34.34,0,0,1,.34.34Zm4.532,8.159a.34.34,0,0,1-.34.34H14.844a.34.34,0,0,1-.34-.34V10.311a.34.34,0,0,1,.34-.34h1.133a.34.34,0,0,1,.34.34Zm0-2.72a.34.34,0,0,1-.34.34H14.844a.34.34,0,0,1-.34-.34V7.592a.34.34,0,0,1,.34-.34h1.133a.34.34,0,0,1,.34.34Z"
