                  class="grid grid-cols-2 gap-1 justify-center w-full"
                  v-for="(value, index) in pharmacy.workingHours"
                  :key="index"
                >
                  <div class="items-end">{{ index }}</div>
                  <div class="text-gray-500">
                    <span> {{ value.start }} lt - </span>
                    <span>{{ value.end }} lt</span>
                  </div>
