          <div class="w-full xl:hidden">
            <div class="flex items-center justify-center" v-if="loading">
              <loading-animation></loading-animation>
            </div>
            <user-review v-else :id="this.id"></user-review>
          </div>
        </div>
      </div>
      <div class="max-w-lg -ml-9 xl:flex hidden">
        <div class="flex items-center justify-center" v-if="reviewLoading">
          <loading-animation></loading-animation>
        </div>
        <user-review v-else :id="this.id"></user-review>
      </div>
    </div>
  </secondary>
</template>

<script>
// import Contact from "../components/DetailPharmacy/Contact.vue";
// import DiscriptionWithImage from "../components/DetailPharmacy/DescriptionWithImage.vue";
import Secondary from "../layouts/Main.vue";
import UserReview from "../components/DetailPharmacy/UserReview";
import { mapGetters } from "vuex";
import LoadingAnimation from "../components/LoadingAnimation.vue";
export default {
  components: {
    Secondary,
    //  Contact,
    // DiscriptionWithImage,
    LoadingAnimation,
    UserReview,
  },
  data() {
    return {
      id: "",
      isFav: false,
      toggled: false,
      loading: true,
      reviewLoading: true,
    };
  },
  name: "PharmacyDetail",
  methods: {
    toggleFavorite() {
      if (this.pharmacy && this.pharmacy.id) {
        console.log("Toggling");
        this.toggled = true;
        this.$store
          .dispatch("favMod/toggleFavorite", this.pharmacy.id)
          .then((res) => {
            this.isFav = res.Status;
            this.toggled = false;
          });
      }
    },
  },
  computed: {
    storageURL() {
      return process.env.VUE_APP_STORAGE_BASE_URL;
      // return '';
    },
    ...mapGetters("searchMedicalCenterMod", {
      pharmacy: "getById",
    }),
  },
  watch: {
    pharmacy(value) {
      this.isFav = value.favorite;
    },
  },
  mounted() {
    this.id = this.$route.query.id;
    this.$store
      .dispatch("searchMedicalCenterMod/getPharmacyById", this.id)
      .then((res) => {
        console.log(res);
        this.loading = false;
      });
    this.$store.dispatch("reviewMod/getReview", this.id).then((res) => {
      console.log(res);
      this.reviewLoading = false;
    });
  },
};
</script>
