// import axios from 'axios';
import api from '../../axios'

const state = {
    newsList: [],
    news: [],
    lastPage:0 , 
    currentPage:0,
    


};
const getters = {
    newsList: (state) => { return state.newsList },
    newsById: (state) => (id) => { return state.newsList.filter((x) => { x.id == id }) },
    news: (state) => { return state.news },
    nextPage:(state) => {return (state.currentPage < state.lastPage)}

};

const actions = {
    async getNewsList({ commit }) {
        console.log("get news list");
        try {
            const response = await api.get('/news');
            commit('NEWSLIST' , response)
            console.log(response);
            return response;
        } catch (error) {
            console.log(error);
            return error;
        }

    },
    async getNews({ commit }, value) {
        console.log("am in the news ",value)
        try {
            const response = await api.get(`/news/${value}`);
            commit('NEWS' , response);
            console.log(response)
            return response;
        } catch (error) {

            return error;
        }
    },
    async getNewsListByPage({ commit }) {

        try {
            const response = await api.get(`/news?page=${state.currentPage+1}`);
            commit('NEXTPAGENEWSLIST', response )
            return response;
        } catch (error) {

            return error;
        }

    },

};
const mutations = {
    NEWSLIST: (state, value) => {
        state.newsList = value.data.data;
        state.currentPage = value.data.current_page;
        state.lastPage = value.data.last_page;
    },
    NEWS: (state, value) => {
        state.news = value.data
    },
    NEXTPAGENEWSLIST: (state, value) => {
        state.currentPage = value.data.current_page;
        state.newsList.push(...value.data.data);
    },
};
export default {
    state,
    getters,
    actions,
    mutations,
    namespaced: true
}