<template>
  <layer class="overflow-x-hidden">
    <div class="flex justify-center">
      <div class="flex h-96 justify-center items-center" v-if="loading">
        <loading-animation></loading-animation>
      </div>
      <div class="flex flex-col gap-8 container max-w-2xl pt-1 mx-3" v-else>
        <!-- {{ user }} -->

        <div class="flex items-center gap-2">
          <span
            class="rounded-full bg-pink-600 text-white w-10 h-10 flex justify-center"
          >
            <span class="text-base my-auto">
              <router-link v-if="user.user.firstName != null" to="/">
                {{ (user.user.firstName + " " + user.user.lastName) | icon }}
              </router-link>
              <router-link v-else to="/">
                {{ "U R" | icon }}
              </router-link>
            </span>
          </span>
          <span
            class="text-xl"
            v-if="
              (user.user && user.user.firstName != null) ||
              (user.user && user.user.lastName != null)
            "
          >
            {{ user.user.firstName + " " + user.user.lastName }}</span
          >
          <span class="text-xl" v-else> Uknown User</span>
        </div>
        <form @submit.prevent="handleSubmit" class="flex flex-col gap-5">
          <div class="flex flex-col w-full gap-2">
            <label for="firstName" class="ml-3 text-lg font-medium"
              >First Name
            </label>
            <input
              id="firstName"
              v-model="firstName"
              type="text"
              autocomplete="off"
              name=""
              class="bg-medehanite-hero w-full h-12 rounded-xl px-4 focus:bg-gray-50 focus:text-black"
            />
          </div>
          <div class="flex flex-col w-full gap-1">
            <label for="firstName" class="ml-3 text-lg font-medium"
              >Last Name
            </label>
            <input
              id="lastName"
              type="text"
              v-model="lastName"
              autocomplete="off"
              name=""
              class="bg-medehanite-hero w-full h-12 rounded-xl px-4 focus:bg-gray-50 focus:text-black"
            />
          </div>
          <div class="flex flex-col w-full gap-1">
            <label for="firstName" class="ml-3 text-lg font-medium"
              >Email
            </label>
            <input
              id="email"
              type="email"
              autocomplete="false"
              v-model="email"
              name=""
              class="bg-medehanite-hero w-full h-12 rounded-xl px-4 focus:bg-gray-50 focus:text-black"
            />
          </div>

          <button
            type="submit"
            class="px-4 py-3 bg-medehanite-primary active:bg-medehanite-light hover:bg-medehanite-focused rounded-lg max-w-max text-base text-white text-center cursor-pointer my-4 flex items-center gap-2"
          >
            <span v-if="spinner">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="25"
                height="25"
                viewBox="0 0 41 41"
                class="animate-spin"
              >
                <g
                  id="Group_3529"
                  data-name="Group 3529"
                  transform="translate(1379 -3270)"
                >
                  <g
                    id="Ellipse_13"
                    data-name="Ellipse 13"
                    transform="translate(-1379 3270)"
                    fill="none"
                    stroke="rgba(112,112,112,0.21)"
                    stroke-width="5"
                  >
                    <circle cx="20.5" cy="20.5" r="20.5" stroke="none" />
                    <circle cx="20.5" cy="20.5" r="18" fill="none" />
                  </g>
                  <g
                    id="Ellipse_14"
                    data-name="Ellipse 14"
                    transform="translate(-1379 3270)"
                    fill="none"
                    stroke="#FFFFFF"
                    stroke-width="5"
                    stroke-dasharray="73 29"
                  >
                    <circle cx="20.5" cy="20.5" r="20.5" stroke="none" />
                    <circle cx="20.5" cy="20.5" r="18" fill="none" />
                  </g>
                </g>
              </svg>
            </span>
            Save Profie
          </button>
        </form>
      </div>
    </div>
  </layer>
</template>
<script>
import { mapGetters } from "vuex";
import LoadingAnimation from "../components/LoadingAnimation.vue";
import Layer from "../layouts/Main.vue";
export default {
  name: "profile",
  components: {
    Layer,
    LoadingAnimation,
  },
  data() {
    return {
      loading: true,
      firstName: "",
      lastName: "",
      emial: "",
      spinner: false,
    };
  },
  watch: {
    user() {
      if (this.user && this.user.user) {
        this.firstName = this.user.user.firstName
          ? this.user.user.firstName
          : "";
        this.lastName = this.user.user.lastName ? this.user.user.lastName : "";
        this.email = this.user.user.email ? this.user.user.email : "";
      }
    },
  },
  computed: {
    ...mapGetters("auth", { user: "user" }),
  },
  methods: {
    handleSubmit() {
      this.spinner = true;
      this.$store
        .dispatch("auth/patchUser", {
          firstName: this.firstName,
          lastName: this.lastName,
          email: this.email,
        })
        .then((res) => {
          if (res.status && res.status == 200) {
            console.log(res.data);
            this.spinner = false;
          }
        });
    },
  },
  mounted() {
    this.$store.dispatch("auth/getUser").then((res) => {
      if (res.status && res.status == 200) {
        this.loading = false;
      } else {
        console.log(res.status);
      }
    });
  },
  filters: {
    icon: function (value) {
      if (!value) return "";
      var caps = value[0];
      for (var i = 0; i < value.length; i++) {
        if (value[i] == " ") {
          if (value[i + 1] == " ") continue;
          caps = caps + value[i + 1];
        }
      }
      return caps;
    },
  },
};
</script>