<template>
  <div
    class="rounded-3xl shadow w-80 h-96 m-1 mb-10 hover:shadow-xl cursor-pointer relative"
    @click="searchMeds"
  >
    <div v-if="loading" class="flex w-full h-full justify-center">
      <loading-animation class="my-auto"></loading-animation>
    </div>
    <div class="h-full w-full rounded-3xl">
      <!-- <div
        class="flex h-full w-full flex-col absolute z-20 jusify-center text-center"
      >
       
      </div> -->

      <div
        v-if="!loading"
        class="absolute z-10 flex justify-center h-full w-80"
      >
        <div
          class="my-auto h-16 bg-medehanite-primary text-white font-medium w-auto flex flex-col justify-center px-6 rounded-full"
        >
          <span class="text-center my-auto"> request has been sent </span>
        </div>
      </div>
      <img
        v-if="!loading"
        :src="`data:image/gif;base64,${image}`"
        class="h-full w-full rounded-3xl object-cover"
      />
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import LoadingAnimation from "../LoadingAnimation.vue";
export default {
  components: { LoadingAnimation },
  name: "ImageCard",
  data() {
    return {
      ext: "",
      image: "",
      response: "",
      loading: true,
      desReqId: "",
      message:""
    };
  },
  props: ["id"],
  computed: {
    ...mapGetters("recentSearchMod", {
      getRecentSearchObject: "getRecentSearchObject",
    }),
    foundImageObj() {
      if (this.getRecentSearchObject(this.id).image != null) {
        console.log(this.getRecentSearchObject(this.id).dispatchRequestId);
        return this.getRecentSearchObject(this.id).dispatchRequestId;
      } else {
        return [];
      }

      // return this.getRecentSearchObject(this.id);
    },
    storageURL() {
      return process.env.VUE_APP_STORAGE_BASE_URL;
    },
  },
  mounted() {
    this.getPrescriptionImage();
  },
  methods: {
    searchMeds() {
      this.$emit("dispatch", this.foundImageObj);
    },
    getPrescriptionImage() {
      let m = this.foundImageObj.split(".", 2);
      this.ext = m[1];
      this.$store
        .dispatch("recentSearchMod/getPreImage", this.foundImageObj)
        .then(
          (res) => {
             console.log("#@!!!!!!!!!!!!!!!!!!!!!!!!!!!!");
            console.log(res);
            console.log("#@!!!!!!!!!!!!!!!!!!!!!!!!!!!!");
            if(res){
            if (res.status == 200) {
              this.image = res.data.image;
            } else if (600 > res.status >= 500) {
              this.message = "There is some thing wrong with the server";
            }
            this.loading = false;
          }}
          // (err) => {
          //   console.log("#@!!!!!!!!!!!!!!!!!!!!!!!!!!!!");
          //   console.log(err);
          //   console.log("#@!!!!!!!!!!!!!!!!!!!!!!!!!!!!");
          // }
        );
    },
  },
};
</script>
