<template>
  <div class="flex ">

    <span class="pr-2 my-auto">{{ rating }}</span>

    <div v-for="(value, index) in 5" :key="index">
      <div class="my-1">
        <!-- empty -->
        <img
          v-if="rating-value<=-1  "
          
          src="../assets/icons/empty-star.svg"
          alt=""
        />
        <!-- half -->
        <img
          v-else-if="rating-value<0 && rating-value>-1  "
          @load="afterDot=0"
          src="../assets/icons/half-star.svg"
          alt=""
        />
        <!-- full -->
        <img
          v-else
          src="../assets/icons/full-star.svg"
          alt=""
        />
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "RatingStarWithNumber",
  props: ["rating"],
  
};
</script>
