import api from "../../axios"
const state = {
    medicalCenterData: {},
    medicalCenters: [],
    switch:false
}
const getters = {
    medicalCenters: (state) => { return state.medicalCenters } , 
    medicalCenterData: (state) => { return state.medicalCenterData } , 
    switch:(state) => {return state.switch}

}
const actions = {
    async toggleFavorite ({commit},value){
        console.log(value);
        try {
            var response = await api.post(`medicalCenters/${ encodeURIComponent(value)}/toggleFavorite`);
            commit('TOGGLE' ,response.data)
            return response.data;
        }catch (err) {
          
            return response.data;
        }
    },
    async getFavoriteMedicalCenters({ commit }) {
        state.medicalCenters = [],
        state.medicalCenterData = {}
        try {
            var response = await api.get('medicalCenters/favorites');
            console.log(response);
            commit('SET_FAV', response.data);
        } catch (err) {
            commit('SET_FAV', []);
        }
    },
    async nextFavorite({commit}){
        try {
            var response = await api.get(state.medicalCenterData.next_page_url);
            commit('SET_FAV', response.data);
        } catch (err) {
            commit('SET_FAV', []);
        }
    }
}
const mutations = {
    SET_FAV: (state, value) => {
        state.medicalCenterData = value;
        console.log('jack the re' , state.medicalCenterData);
        state.medicalCenters.push(...value.data);
    },
    TOGGLE:(state,value) =>{
        state.switch = value.Status;
        console.log(state.switch)
    }
}
export default {
    state,
    getters,
    actions,
    mutations,
    namespaced: true
}