    <p class="text-gray-400 text-center mr-5">
      Enter the code sent
      <span v-if="userPhone && userPhone.length>0" class="text-black">
        to {{ userPhone }}</span
      >
    </p>

    <div class="w-full py-4 px-10">
      <div>
        <div class="relative text-gray-600">
          <div class="flex justify-center">
            <!-- <div v-for="(value, index) in 6" :key="index" class="pr-4">
              <input
                autocomplete="off"
                data-lpignore="true"
                :ref="'inputField' + index"
                v-model="otpData[index]"

                class="w-12 mb-4  rounded border border-gray-200 p-3 text-center appearance-none  mr-2"

                type="text"
                maxlength="1"
                @input="next($event)"
              />
            </div> -->
            <input
              autocomplete="off"
              data-lpignore="true"
              :ref="'inputField' + 0"
              v-model="otpData[0]"
              class="mb-4 rounded border border-gray-200 p-3 text-center appearance-none mr-2 w-full"
              type="text"
              placeholder="Enter a 6 digit code here ->"
              maxlength="6"
            />
            <!-- <input
              autocomplete="off"
              data-lpignore="true"
              :ref="'inputField' + 1"
              v-model="otpData[1]"
              class="w-12 mb-4  rounded border border-gray-200 p-3 text-center appearance-none  mr-2"
              type="text"
              maxlength="1"
              @input="next($event, 1)"
            />
            <input
              autocomplete="off"
              data-lpignore="true"
              :ref="'inputField' + 2"
              v-model="otpData[2]"
              class="w-12 mb-4  rounded border border-gray-200 p-3 text-center appearance-none  mr-2"
              type="text"
              maxlength="1"
              @input="next($event, 2)"
            />
            <input
              autocomplete="off"
              data-lpignore="true"
              :ref="'inputField' + 3"
              v-model="otpData[3]"
              class="w-12 mb-4  rounded border border-gray-200 p-3 text-center appearance-none  mr-2"
              type="text"
              maxlength="1"
              @input="next($event, 3)"
            />
            <input
              autocomplete="off"
              data-lpignore="true"
              :ref="'inputField' + 4"
              v-model="otpData[4]"
              class="w-12 mb-4  rounded border border-gray-200 p-3 text-center appearance-none  mr-2"
              type="text"
              maxlength="1"
              @input="next($event, 4)"
            />
            <input
              autocomplete="off"
              data-lpignore="true"
              :ref="'inputField' + 5"
              v-model="otpData[5]"
              class="w-12 mb-4  rounded border border-gray-200 p-3 text-center appearance-none "
              type="text"
              maxlength="1"
              @input="next($event, 5)"
            /> -->
          </div>
        </div>
      </div>
      <div class="flex justify-end">
        <span class="text-blue-500 cursor-pointer">resend</span>
      </div>
    </div>
    <div class="pb-5 flex w-full justify-center">
      <button
        v-if="!loading"
        @click="submitCode"
        type="submit"
        class="text-white h-12 w-48 bg-medehanite-primary rounded-full focus:shadow-outline hover:bg-medehanite-focused"
      >
        Continue
      </button>
      <loading-animation v-if="loading"></loading-animation>
    </div>
  </authentication-layout>
</template>
<script>
import { mapGetters } from "vuex";
import AuthenticationLayout from "../../layouts/Authentication";
import LoadingAnimation from "../../components/LoadingAnimation.vue";

export default {
  name: "OTP",
  components: {
    AuthenticationLayout,
    LoadingAnimation,
  },
  data() {
    return {
      otpData: ["", "", "", "", "", ""],
      loading: false,
    };
  },
  watch: {
    otpData() {
      console.log(this.otpData);
    },
  },
  methods: {
    async submitCode() {
      this.loading = true;
      if(this.userPhone && this.userPhone.length >0){

      
      try {
        let loginResult = await this.$store
          .dispatch("auth/login", this.userPhone)
          .then((result) => {
            if (result.success) {
               this.$api.defaults.headers.common["Authorization"] =
          "Bearer " + localStorage.getItem("token");
              this.loading = false;
              this.$router.push({
                path: "/",
              });
            } else {
              this.loading = false;
              this.err = false;
            }
          });
       
        console.log(loginResult);
      } catch (e) {
        console.log(e);
        //:Todo vaidate
      }}else{
          this.$router.push({
                path: "/login",
              });
      }
    },

    next: function ($event, index) {
      // this.$nextTick(function() {
      //   this.$refs.email.focus();
      // });
      if (index != 5) {
        $event.target.nextElementSibling.focus();
      }
    },
  },
  computed: {
    ...mapGetters("auth", {
      userPhone: "phoneNumber",
    }),
  },
};
</script>
<style scoped>
input[name="number"] {
  border: solid;
  border-color: rgb(204, 204, 204);
  border-width: thin;
}
input[name="number"]:focus {
  border-color: rgba(204, 204, 204, 0.041);
}
</style>
