// import axios from 'axios';
import api from '../../axios'

const state = {
    suggestions:[]

};
const getters = {
    suggestions: (state) => state.suggestions,
};

const actions = {
    // async fetchTodos({ commit }) {
    //     console.log(api);
    //     const response = await api.get('todos');
    //     console.log(response.data);
    //     commit('setTodos', response.data);

    // },
    async suggestion({ commit }, value) {
        if(value.length > 0){
        try {
            const response = await api.get(`medicines/suggestions?name=${encodeURIComponent(value)}`);
            console.log(response.data);
            commit('SET_SUGGESTION',response.data.suggestions)
            return { 'success': true };
        } catch (err) {
            return { 'success': false };
        }
    }else{
        commit('SET_SUGGESTION',[])
    }}

};
const mutations = {
    SET_SUGGESTION: (state, value) => (state.suggestions = value),
};
export default {
    state,
    getters,
    actions,
    mutations,
    namespaced: true
}