// medicalCenter/8ca1b364-3d15-4344-916d-76a5d3a41bb8/review
// import axios from 'axios';
import api from '../../axios'

const state = {
    review: {},
    reviewData: []

};
const getters = {
    reviewData: (state) => state.reviewData,
    review: (state) => state.review,
};

const actions = {

    async getReview({ commit }, value) {
        if (value.length > 0) {
            try {
                const response = await api.get(`medicalCenter/${value}/review`);
                console.log(response.data);
                commit('SET_REVIEW', response.data);
                return { 'success': true, 'valid': true };
            } catch (err) {
                var status = ""
                if(err.response){
                    status = err.response.status
                }
                return { 'success': false , 'status':status};
            }
        } else {
            commit('SET_REVIEW', [])
        }
    },
    async addReview({ commit }, value) {

            state.medSearchName = value;
            try {
                const response = await api.post(`medicalCenter/${value.id}/review`, { "rating": value.rating, "review": value.review });
                commit('SET_REVIEWADD', response.data);
                return { 'success': true, 'valid': true };
            } catch (err) {
                var status = ""
                if(err.response){
                    status = err.response.status
                }
                return { 'success': false , 'status':status};
            }
    },
};
const mutations = {
    SET_REVIEW: (state, value) => {
        state.review = value,
            state.reviewData = value.reviews.data
    },
    SET_REVIEWADD: (value) => {
        // state.review = value,
           
        console.log(value)
    },
};
export default {
    state,
    getters,
    actions,
    mutations,
    namespaced: true
}