<template>
  <div class="rounded-3xl shadow w-80 m-1 mb-10 hover:shadow-xl cursor-pointer" @click="search">
    <img
      v-if="!isUnfound"
      :src="storageURL + fullInfo.image"
      class="h-48 w-full bg-medehanite-light rounded-t-3xl pt-4 m-auto object-scale-down hover:object-contain card-header"
    />
    <img
      v-else
      src="@/assets/images/mucinex.png"
      class="h-48 w-full bg-medehanite-light rounded-t-3xl pt-4 m-auto object-scale-down hover:object-contain card-header"
    />

    <div v-if="!isUnfound" class="mx-auto">
      <div class="text-xl font-semibold pt-5 pb-2 text-center">
        <div class="mx-2 truncate">
          {{ fullInfo.brandName }}
          <hr class="w-1/2 mx-auto bg-medehanite-primary h-1 mt-2" />
        </div>
      </div>
    </div>
     <div v-if="isUnfound" class="mx-auto">
      <div class="text-xl font-semibold pt-5 pb-2 text-center">
        <div class="mx-2 truncate">
          {{ fullInfo }}
          <hr class="w-1/2 mx-auto bg-medehanite-primary h-1 mt-2" />
        </div>
      </div>
    </div>

    <div v-if="!isUnfound" class="text-base p-6 text-gray-600 mx-2">
      <div class="text-center">
        <span class="font-bold pr-1 text-black">GenericName:</span>

        <span class="text-left"> {{ fullInfo.genericName }}</span>
      </div>
      <div class="text-center">
        <span class="font-bold pr-1 text-black">Manufacturer:</span>
        {{ fullInfo.manufacturer }}
      </div>
    </div>

  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  name: "MedcineCard",
  data() {
    return {
      isUnfound: false,
    };
  },
  props: ["id"],
  computed: {
    ...mapGetters("recentSearchMod", {
      getRecentSearchObject: "getRecentSearchObject",
    }),
    fullInfo() {
      if (this.getRecentSearchObject(this.id).found_meds_obj[0] != null) {
        return this.getRecentSearchObject(this.id).found_meds_obj[0];
      } else {
        return this.getRecentSearchObject(this.id).unfoundMeds;
      }
      
      // return this.getRecentSearchObject(this.id);
    },
     storageURL() {
      return process.env.VUE_APP_STORAGE_BASE_URL;
    },
  },
  watch: {
    fullInfo(value) {
      if (typeof value == typeof "") {
        this.isUnfound = true;
      } else {
        this.isUnfound = false;
      }
    },
  },
  methods: {
   search(){
     if(this.isUnfound){
         this.$router.push({
    name: "fi-pharmacy",
    query: { found: "",notFound:this.fullInfo }
  });
     }else{
        this.$router.push({
    name: "fi-pharmacy",
    query: { found: this.fullInfo.id,notFound:"" }
  }); 
     }
   }
  },
};
</script>
