// import axios from 'axios';
import api from '../../axios'

const state = {
    pharmacyData: {},
    pharmacies: [],
    pharmacyWithMed: {},
    pharmacyById: {},
    medicalCenterData: {},
    MedCenters: [],

};
const getters = {
    getpharmacyData: (state) => { return state.pharmacyData },
    getPharmacies: (state) => state.pharmacies,
    getPharmaciesWithMed: (state) => state.pharmacyWithMed,
    getMeds: (state) => state.pharmacyWithMed.searchedMeds,
    getUnfoundMeds: (state) => state.pharmacyWithMed.unfoundMeds,
    getById: (state) => state.pharmacyById,
    getMedCenters: (state) => state.MedCenters,
    getMedCentersData: (state) => state.medicalCenterData

};

const actions = {

    async getPharmacyByName({ commit }, value) {
        state.pharmacies = []
        state.pharmacyData = {}
        if (value.length > 0) {
            try {
                // let value = this.$store.getters["suggestion/suggestions"];
                const response = await api.post('medicalCenters', {
                    "type": ["Pharmacy", "Clinic", "Hospital"],
                    "longitude": 1,
                    "latitude": 1,
                    "proximity": 1000000000,
                    "name": value
                });
                console.log(response.data.data);
                console.log(response.data);
                commit('SET_PHARM', response.data)
                return { 'success': true };
            } catch (err) {
                return { 'success': false };
            }
        } else {
            commit('SET_Pharm', [])
        }
    },
    async nextPharmacies({ commit }) {
        try {
            var response = await api.get(state.pharmacyData.next_page_url);
            commit('SET_PHARM', response.data)
            return response;
        } catch (err) {
            return err;
        }
    },

    async getMedCenterByName({ commit }, value) {
        state.medicalCenterData = {}
        state.MedCenters = []
        if (value.length > 0) {
            try {
                // let value = this.$store.getters["suggestion/suggestions"];
                const response = await api.post('medicalCenters', {
                    "type": ["Pharmacy", "Clinic", "Hospital"],
                    "longitude": 1,
                    "latitude": 1,
                    "proximity": 1000000000,
                    "name": value
                });
                commit('SET_MED_CENTERS', response.data)
                return { 'success': true };
            } catch (err) {
                return { 'success': false };
            }
        } else {
            commit('SET_MED_CENTERS', [])
        }
    },
    async nextMedicalCenter({ commit }) {
        try {
            var response = await api.get(state.medicalCenterData.next_page_url);
            commit('SET_MED_CENTERS', response.data)
            return response;
        } catch (err) {
            return err;
        }
    },
    async getPharmacyByMed({ commit }, value) {

        if (value.found == "") {
            value.found = null
        }
        console.log(value);
        try {
            console.log("am here");
            // let value = this.$store.getters["suggestion/suggestions"];
            const response = await api.post('medicalCenters/medicines', {

                "medicineIds": value.found,
                "page": 1,
                "unfoundMeds": value.notFound,
                "longitude": 1,
                "latitude": 1,
                "proximity": 10000000

            });
            console.log(response);
            // console.log(response.data.data);
            commit('SET_PHARM_WITH_MED', response.data)
            return { 'success': true };
        } catch (err) {
            return { 'success': false };
        }

    },
    async getPharmacyById({ commit }, value) {


        if (value.length > 0) {
            try {
                // let value = this.$store.getters["suggestion/suggestions"];
                const response = await api.get(`medicalCenters/${value}`);
                commit('SET_SINGLE_PHARMACY', response.data)
                console.log(response.data)
                return { 'success': true };
            } catch (err) {
                return { 'success': false };
            }
        } else {
            commit('SET_SINGLE_PHARMACY', [])
        }

    },

};

const mutations = {
    SET_PHARM: (state, value) => {
        state.pharmacies.push(...value.data);
        state.pharmacyData = value;
    },
    SET_MED_CENTERS: (state, value) => {
        state.medicalCenterData = value
        state.MedCenters.push(...value.data) },
    SET_PHARM_WITH_MED: (state, value) => (state.pharmacyWithMed = value),
    SET_SINGLE_PHARMACY: (state, value) => (state.pharmacyById = value)
};
export default {
    state,
    getters,
    actions,
    mutations,
    namespaced: true
}