          <div>
            <div class="flex flex-col">
              <div>Rate and Review your experiance</div>
              <div class="py-2">
                <div class="flex gap-2">
                  <span class="pr-2 my-auto">{{ rating }}</span>

                  <div v-for="(value, index) in 5" :key="index">
                    <div class="my-1 cursor-pointer" @click="rating = value">
                      <!-- empty -->
                      <img
                        v-if="rating - value <= -1"
                        src="../../assets/icons/empty-star.svg"
                        alt=""
                      />
                      <!-- half -->
