</template>
<script>
import RatingStarWithNumber from "../RatingStarWithNumber";
import { mapGetters } from "vuex";
export default {
  name: "reviewlister",
  components: { RatingStarWithNumber },
  data() {
    return {
      rating: 1,
      review: "",
      error: false,
      errorString: "",
    };
  },
  props: ["id"],
  computed: {
    ...mapGetters("reviewMod", {
      reviewData: "reviewData",
      totalReview: "review",
    }),
  },
  methods: {
    sendReview() {
      this.$store
        .dispatch("reviewMod/addReview", {
          review: this.review,
          rating: this.rating,
          id: this.id,
        })
        .then((result) => {
          if (result.status && result.status == 403) {
            this.errorString = "you have already given review to this phamracy";
            this.error = true;
          }else{
                this.$store.dispatch("reviewMod/getReview", this.id);
          }
        });
      console.log(this.review, this.rating, this.id);
    },
  },
};
</script>

