// import axios from 'axios';
import api from '../../axios'

const state = {
    phar: [],
    type: ""
};
const getters = {
    pharmacies: (state) => state.phar,
};

const actions = {

    setState(st, value) {
        state.type = value;
        console.log(state.type);
        console.log('###########');
    },
    async getPharmacyByLocation({ commit }, location) {
        try {
            const response = await api.post('medicalCenters', {
                "type": ["Pharmacy", "Clinic", "Hospital"],
                "longitude": location.longitude,
                "latitude": location.latitude,
                "proximity": 5,
                "limit": 20
            });
            commit('SET_PHARM', response.data)
            return { 'success': true };
        } catch (err) {
            return { 'success': false };
        }
    },
    async getPharmacyByName({commit}, data) {
        var type = []

        if (data.type == 'Clinic') { type = ['Clinic'] }
        else if (data.type == 'Pharmacy') { type = ['Pharmacy'] }
        else if (data.type == 'Hospital') { type = ['Hospital'] }
        else { type = ["Pharmacy", "Clinic", "Hospital"]; }


        try {
            const response = await api.post('medicalCenters', {
                "type": type,
                "longitude": 1,
                "latitude": 1,
                "proximity": 100000000000000000000,
                "name": data.name,
            });
            commit('SET_PHARM', response.data)
            return { 'success': true };
        } catch (err) {
            return { 'success': false };
        }
    },




};
const mutations = {
    SET_PHARM: (state, result) => { state.phar = result.data },
};
export default {
    state,
    getters,
    actions,
    mutations,
    namespaced: true
}