<script>
import { mapGetters } from "vuex";
export default {
  name: "PhamracyCadWithMedicine",
  props: [
    "name",
    "rating",
    "medsFound",
    "unFoundedMeds",
    "relativeLocation",
    "longitude",
    "latitude",
    "id",
    "logo",
    "medName",
    "purchaseId"
  ],
  computed: {
    ...mapGetters("searchMedicalCenterMod", {
      meds: "getMeds",
    }),
    storageURL() {
      return process.env.VUE_APP_STORAGE_BASE_URL;
    },
  },
  methods: {
    getMed(value) {
      return this.meds[value].brandName;
    },
    vote() {
      this.$store
        .dispatch("voteAndOrderMod/voteForPurchase", this.purchaseId)
        .then((res) => {
          if (res.status != 200) {
            this.$toast.open({
              message: "You have already voted for this medication!!",
              type: "error",
            });
          } else {
            this.$toast.open("successfully voted");
          }
        });
    },
    gotoPharmacyDetail() {
      this.$router.push({
        path: "phar-detail",
        query: { id: this.id },
      });
    },
    gotoMap(long, lat) {
      console.log(long, lat);
      // let currentLocation = this.$store.getters["location/currentLoaction"];
      window.open(`https://www.google.com/maps/@${long},${lat}`);
      // if(this.currentLocation!={}){
      // // window.open(`https://www.google.com/maps/dir/${currentLocation.longitude} ,${currentLocation.latitude}/${long},${lat}/`);
      // window.open(`https://www.google.com/maps/dir/${currentLocation.longitude} ,${currentLocation.latitude}/${long},${lat}/`);
      // }else{
      //   window.open(`https://www.google.com/maps/@${long},${lat}`)
      // }
    },
  },
};
</script>
