import api from "../../axios"
const state = {
    faqData: {},
    faqs: []
}
const getters = {
    faqs: (state) => { return state.faqs } , 
    faqData: (state) => {return state.faqData}
}
const actions = {
    async getFAQ({ commit }) {
        state.faqs = [],
        state.faqData = {}
        try {
            var response = await api.get('faq');
       
            commit('SET_FAQ', response.data);
        } catch (err) {
            commit('SET_FAQ', []);
        }
    },
    async nextFAQ({commit}){
        try {
            var response = await api.get(state.faqData.next_page_url);
       
            commit('SET_FAQ', response.data);
        } catch (err) {
            commit('SET_FAQ', []);
        }
    }
}
const mutations = {
    SET_FAQ: (state, value) => {
        state.faqData = value;
        console.log('jack the re' , state.faqData);
        state.faqs.push(...value.data);
    }
}
export default {
    state,
    getters,
    actions,
    mutations,
    namespaced: true
}