import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Login from "../views/Authentication/login";
import OTP from "../views/Authentication/OTP";
import SingUp from "../views/Authentication/SignUp.vue";
import PharmacyDetail from "../views/PhamracyDetail.vue";
import FindInPharmacyMedicine from "../views/Search/FindInPharmacyResult.vue";
import Profile from "../views/Profile.vue";
import NotFound from "../views/404.vue"

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/about",
    name: "About",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue"),
  },
  {
    path: "/search",
    name: "Search",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/Search.vue"),
  },
  {
    path: "/login",
    name: "login",
    component: Login,
  },
  {
    path: "/otp",
    name: "otp",
    component: OTP,
  },
  {
    path: "/signup",
    name: "SignUp",
    component: SingUp
  },
  {
    path: "/phar-detail",
    name: "PharmacyDetail",
    component: PharmacyDetail
  },
  {
    path: "/fi-pharmacy",
    name: "fi-pharmacy",
    component: FindInPharmacyMedicine
  },
  {
    path: "/phar-registration",
    name: "PharmacyRegistration",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/Registration/Pharmacy.vue"),
  },
  {
    path: "/nearby",
    name: "nearBy",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/NearBy.vue"),
  },
  {
    path: "/news",
    name: "news",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/News.vue"),
  },
  {
    path: "/news-detail",
    name: "NewsDetail",
    component: () =>
      import(/* webpackChunkName: "about" */ "../components/NewsDetail.vue"),
  },
  {
    path: "/profile",
    name: "profile",
    component: Profile
  },
  {
    path: "/faq",
    name: "faq",
    component: () =>
    import(/* webpackChunkName: "about" */ "../views/FAQ.vue"),
  },
  {
    path: "/fav-pharm",
    name: "favorite-pharmacy",
    component: () =>
    import(/* webpackChunkName: "about" */ "../views/FavoritePharmacy.vue"),
  },
  {
    path: "/privacy",
    name: "privacy",
    component: () =>
    import(/* webpackChunkName: "about" */ "../views/PrivacyPolicy.vue"),
  },
  {
    path: "/terms",
    name: "terms",
    component: () =>
    import(/* webpackChunkName: "about" */ "../views/TermsAndCondition.vue"),
  },
    {
    path: "/contact",
    name: "contact",
    component: () =>
    import(/* webpackChunkName: "about" */ "../views/ContactUs.vue"),
  },
  {
    path: "*",
    name: "notFound",
    component: NotFound
  },
];

const router = new VueRouter({

  routes,
  mode: "history",
  scrollBehavior() {
    return new Promise((resolve) => {
      
      setTimeout(() => {
        
        resolve({ x: 0, y: 0  , behavior:'smooth'} )
      }, 500)
    })
  }
});

export default router;
