<template>
  <div class="rounded-3xl shadow w-80 m-1 mb-10 hover:shadow-xl cursor-pointer" @click="searchMeds">
    <img
      v-if="!isUnfound"
      :src="storageURL + foundMedObj[0].image"
      class="h-48 w-full bg-medehanite-light rounded-t-3xl pt-4 m-auto object-scale-down hover:object-contain card-header"
    />
    <img
      v-else
      src="@/assets/images/mucinex.png"
      class="h-48 w-full bg-medehanite-light rounded-t-3xl pt-4 m-auto object-scale-down hover:object-contain card-header"
    />

    <div class="mx-auto overflow-y-hidden" 
    style="max-height:12rem">
      <div v-for="(value, index) in foundMedObj" :key="value.id + index">
        <div class="text-base  text-start text-gray-700 mx-5 mt-1">
          <div class=" truncate">
            {{ " . "+ value.brandName}}
            <!-- <hr class="w-1/2 mx-auto bg-medehanite-primary h-1 mt-2" /> -->
          </div>
        </div>
      </div>
      <div v-for="(value, index) in UnFoundMedObj" :key="index + value">
        <div class="text-base  text-start text-red-500 mx-5 mt-1">
          <div class=" truncate">
            {{" . "+ value }}
            <!-- <hr class="w-1/2 mx-auto bg-medehanite-primary h-1 mt-2" /> -->
          </div>
        </div>
      </div>
    </div>

  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  name: "MedcineCard",
  data() {
    return {
      isUnfound: false,
    };
  },
  props: ["id"],
  computed: {
    ...mapGetters("recentSearchMod", {
      getRecentSearchObject: "getRecentSearchObject",
    }),
    foundMedObj() {
      if (this.getRecentSearchObject(this.id).found_meds_obj[0] != null) {
        return this.getRecentSearchObject(this.id).found_meds_obj;
      } else {
        return [];
      }

      // return this.getRecentSearchObject(this.id);
    },
    UnFoundMedObj() {
      if (this.getRecentSearchObject(this.id).unfoundMeds[0] != null) {
        return this.getRecentSearchObject(this.id).unfoundMeds;
      } else {
        return [];
      }
    },
    storageURL() {
      return process.env.VUE_APP_STORAGE_BASE_URL;
    },
  },
  watch: {
    foundMedObj(value) {
      if (typeof value == []) {
        this.isUnfound = true;
      } else {
        this.isUnfound = false;
      }
    },
  },
  methods: {
    searchMeds(){
      // var medId=[]
     var found= this.foundMedObj.map((x)=>{
        if(x.id){
        return x.id;}else return ""
      }).toString()
var unfound = this.UnFoundMedObj.map(x=>x).toString();
      this.$router.push({
    name: "fi-pharmacy",
    query: { found: found,notFound:unfound }
  });
    }
  },
};
</script>
