<template>
  <layout class="overflow-x-hidden">
    <image-search-result :id="dispatchReqId"></image-search-result>
    <div
      class="ml-auto w-full flex relative bg-medehanite-hero bg-left justify-around"
    >
      <upload @upload="startUpload"></upload>
      <div class="my-auto flex-row">
        <intersection-observer
          @intersect="$eventbus.$emit('landing-page-intersecting')"
          @disentagle="$eventbus.$emit('landing-page-disentagling')"
        >
          <div class="text-5xl md:text-7xl font-bold top-96 mt-4 pl-3 lg:pl-0">
            {{ $t("message.home.greetingOne") }}
            <span class="text-medehanite-primary font-extrabold">{{
              $t("message.home.greetingTwo")
            }}</span
            ><br />{{ $t("message.home.greetingThree") }}
            <span class="text-medehanite-primary font-extrabold">{{
              $t("message.home.greetingFour")
            }}</span>
            {{ $t("message.home.greetingFive") }}
          </div>
        </intersection-observer>
        <div class="relative px-2">
          <div
            class="transition-all duration-500 absolute h-24 flex flex-col justify-center bg-green-400 rounded-lg z-40 w-full my-10 text-gray-600 px-8"
            :class="{ hidden: onUpload }"
          >
            <div
              class="rounded-full absolute bg-white w-7 h-7 flex flex-col justify-center text-center right-0 top-0 -mr-3 -mt-3 cursor-pointer"
              @click="onUpload = true"
            >
              <div class="text-red-500 font-bold text-2xl">x</div>
            </div>
            <div class="flex mb-2 items-center justify-between">
              <div>
                <span
                  class="text-xs font-semibold inline-block py-1 px-2 uppercase rounded-full text-pink-600 bg-pink-200"
                >
                  {{ $t("message.home.taskInProgress")}}
                </span>
              </div>
              <div class="text-right">
                <span class="text-xs font-semibold inline-block text-pink-600">
                  {{ progress }}
                </span>
              </div>
            </div>
            <div
              class="overflow-hidden h-2 mb-4 text-xs flex rounded bg-pink-200"
            >
              <div
                :style="{ width: progress }"
                class="shadow-none flex flex-col text-center whitespace-nowrap text-white justify-center bg-pink-500"
              ></div>
            </div>
          </div>
          <div
            class="relative w-full shadow rounded-full p-2 bg-white inline-flex top-12 text-gray-600 focus-within:text-gray-400 h-14 mb-20 xl:my-auto sm:h-12"
          >
            <span
              class="absolute rounded-full inset-y-0 left-2 flex items-center px-2"
            >
              <svg
                fill="none"
                stroke="currentColor"
                stroke-linecap="round"
                stroke-linejoin="round"
                stroke-width="2"
                viewBox="0 0 24 24"
                class="w-6 h-6 top-0"
              >
                <path d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"></path>
              </svg>
            </span>
            <input
              type="search"
              name="q"
              class="py-2 rounded-full text-base w-full text-white bg-white pl-11 pr-28 focus:outline-none focus:bg-white focus:text-gray-900"
              :placeholder="$t('message.home.searchHere')"
              autocomplete="off"
              v-model="medName"
              @keydown.enter="searchMed"
              @focus="showSuggestion = true"
              @click="showSuggestion = true"
            />
            <div class="absolute inline-flex right-0 h-full top-0">
              <button
                @click="modal"
                class="border-l rounded-full rounded-l-none inset-y-0 flex items-center px-5 pl-3 hover:bg-medehanite-hero"
              >
                <img
                  src="@/assets/camera-icon.svg"
                  class="fill-medehanite-primary w-6 h-6"
                />
              </button>
              <input
                type="file"
                ref="fileInput"
                @change="onFileChange($event)"
                name=""
                class="hidden"
                accept="image/*"
              />
            </div>
            <div
              class="absolute z-50 bg-white shadow-md rounded-lg w-full my-12 flex-col py-2 text-black"
              v-click-outside="clickaway"
              v-if="showSuggestion && hasValue"
            >
              <div
                class="bg-white hover:bg-gray-200 w-full h-12 flex transition-transform duration-1000 cursor-pointer"
                @click="setSearchInputValue(value), (showSuggestion = false)"
                v-for="(value, index) in suggestions"
                :key="index"
              >
                <span class="flex px-5">
                  <svg
                    fill="none"
                    stroke="currentColor"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="2"
                    viewBox="0 0 24 24"
                    class="w-5 h-5 my-auto justify-center"
                  >
                    <path
                      d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                    ></path>
                  </svg>
                </span>

                <p class="my-auto">{{ value }}</p>
              </div>
            </div>
          </div>
        </div>
        <div class="relative pt-1"></div>
      </div>
      <img class="hidden xl:flex" src="@/assets/images/hero_doctor.svg" />
    </div>

    <div class="relative z-20">
      <div class="z-10 fixed right-2 -mt-6" @click="showLang" v-click-outside="clickawayLang">
        <div
          
          class="bg-medehanite-primary rounded-full w-12 h-12 flex justify-center items-center cursor-pointer"
        >
          <div class="rounded-full w-7 h-7 flex justify-center items-center">
            <img src="../assets/icons/translate.svg" alt="" />
          </div>
        </div>
        <div
          v-if="isLangShow"
          class="absolute w-40 bg-white ring-2 ring-opacity-30 ring-gray-300 top-9 float-right rounded-lg shadow-md flex-col pt-5 pb-4 gap-1 right-0 cur"
        >
          <div
            class="text-base text-gray-500 w-full hover:bg-gray-200 hover:text-black px-5 py-2 cursor-pointer"
            v-for="lan in languages"
            :key="lan"
          >
            <div @click="$i18n.locale = lan">
              {{ lan }}
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="bg-white mt-8 py-1 pb-5">
      <div class="container mx-auto flex-1 flex justify-center">
        <div class="text-4xl font-bold pt-5 pb-2">
           {{ $t("message.home.ourServices") }}
          <hr class="w-3/4 mx-auto bg-medehanite-primary h-1 mt-2" />
        </div>
      </div>
    </div>
    <div class="bg-white">
      <div class="container h-full mx-auto">
        <div class="flex flex-wrap justify-around">
          <div class="relative flex flex-col">
            <div class="relative w-80 h-40">
              <div
                class="absolute left-32 w-20 h-20 mx-auto rounded-full bg-medehanite-primary border-4 border-white z-10 text-white text-xl font-bold flex flex-col text-center justify-end"
              >
                <img src="@/assets/images/pill.svg" class="p-4 text-white" />
              </div>
              <div
                class="absolute left-0 top-10 w-80 bg-medehanite-primary h-20 rounded-md shadow text-white text-xl z-0 font-bold flex flex-col text-center justify-end pb-2"
              >
                
                  {{ $t("message.home.NearbyPharmacies") }}
              </div>
            </div>

            <div class="bg-white px-4 py-6 mx-1 w-80 h-full">
              <div class="mx-auto text-lg font-bold text-center h-8">
                Pharmacies
              </div>
              <div class="text-base text-center text-gray-600">
                Lorem ipsum dolor, sit amet consectetur adipisicing elit. Quo
                animi quos nemo sed. Neque nobis, dolore nostrum ullam saepe
                sit? Odio eveniet aliquid deleniti vitae, minus alias quos omnis
                necessitatibus?
              </div>
            </div>
          </div>

          <div class="relative flex flex-col">
            <div class="relative w-80 h-40">
              <div
                class="absolute left-32 w-20 h-20 mx-auto rounded-full bg-red-400 border-4 border-white z-10 text-white text-xl font-bold flex flex-col text-center justify-end"
              >
                <img src="@/assets/images/cross.svg" class="p-4" />
              </div>
              <div
                class="absolute left-0 top-10 w-80 bg-red-400 h-20 rounded-md shadow text-white text-xl z-0 font-bold flex flex-col text-center justify-end pb-2"
              >
                
                  {{ $t("message.home.NearbyClinics") }}
              </div>
            </div>

            <div class="bg-white px-4 py-6 mx-1 w-80 h-full">
              <div class="mx-auto text-lg font-bold text-center h-8">
                Clinics
              </div>
              <div class="text-base text-center text-gray-600">
                Lorem ipsum dolor, sit amet consectetur adipisicing elit. Quo
                animi quos nemo sed. Neque nobis, dolore nostrum ullam saepe
                sit? Odio eveniet aliquid deleniti vitae, minus alias quos omnis
                necessitatibus?
              </div>
            </div>
          </div>

          <div class="relative flex flex-col">
            <div class="relative w-80 h-40">
              <div
                class="absolute left-32 w-20 h-20 mx-auto rounded-full bg-yellow-400 border-4 border-white z-10 text-white text-xl font-bold flex flex-col text-center justify-end"
              >
                <img src="@/assets/images/doctor.svg" class="p-4" />
              </div>
              <div
                class="absolute left-0 top-10 w-80 bg-yellow-400 h-20 rounded-md shadow text-white text-xl z-0 font-bold flex flex-col text-center justify-end pb-2"
              >
                
                  {{ $t("message.home.ContactDoctor") }}
              </div>
            </div>

            <div class="bg-white px-4 py-6 mx-1 w-80 h-full">
              <div class="mx-auto text-lg font-bold text-center h-8">
                Doctors
              </div>
              <div class="text-base text-center text-gray-600">
                Lorem ipsum dolor, sit amet consectetur adipisicing elit. Quo
                animi quos nemo sed. Neque nobis, dolore nostrum ullam saepe
                sit? Odio eveniet aliquid deleniti vitae, minus alias quos omnis
                necessitatibus?
              </div>
            </div>
          </div>
        </div>
        <div></div>
      </div>
    </div>

    <div class="container bg-white mx-auto rounded-2xl shadow-sm p-8 mt-6">
      <div v-if="showLoading" class="flex justify-center">
        <loading-animation></loading-animation>
      </div>
      <div
        class="container mx-auto"
        v-if="!showLoading && getRecentSearch.length > 0"
      >
        <div
          class="text-2xl font-semibold text-medehanite-primary h-14 mt-2 pl-4"
        >
          {{ $t("message.home.recentSearches") }}
        </div>

        <div class="relative w-full mx-auto flex flex-row">
          <div class="absolute inset-y-0 left-0 z-20 flex items-center">
            <button
              @click="scrollLeft"
              class="bg-white -ml-2 lg:-ml-4 flex justify-center items-center w-10 h-10 rounded-full shadow-lg focus:outline-none"
            >
              <svg
                viewBox="0 0 20 20"
                fill="currentColor"
                class="chevron-left w-6 h-6"
              >
                <path
                  fill-rule="evenodd"
                  d="M12.707 5.293a1 1 0 010 1.414L9.414 10l3.293 3.293a1 1 0 01-1.414 1.414l-4-4a1 1 0 010-1.414l4-4a1 1 0 011.414 0z"
                  clip-rule="evenodd"
                ></path>
              </svg>
            </button>
          </div>
          <div class="flex w-full">
            <div class="swiper-wrapper flex w-full justify-center">
              <div
                id="parent"
                ref="parent"
                class="grid grid-flow-col gap-8 overflow-x-scroll scroll-hider bg-scroll ml-4"
              >
                <!-- Slides -->

                <!-- <div v-for="(value, index) in getRecentMeds" :key="index">
                  <div>
                    <medicine-card :id="value"></medicine-card>
                  </div>
                </div> -->

                <div v-for="(value, index) in getRecentSearch" :key="index">
                  <div v-if="value.stack">
                    <stacked-medicine-card
                      :id="value.id"
                    ></stacked-medicine-card>
                  </div>
                  <div v-else>
                    <div v-if="value.image != null">
                      <image-card
                        :id="value.id"
                        @dispatch="setDesReqId($event)"
                      ></image-card>
                    </div>
                    <div v-else>
                      <medicine-card :id="value.id"></medicine-card>
                    </div>
                  </div>
                </div>
                <div
                  class="flex justify-center items-center pr-12"
                  v-if="nextRecentLoading"
                >
                  <loading-animation></loading-animation>
                </div>
                <intersection-observer
                  @intersect="nextRecent"
                ></intersection-observer>
                <div class="w-9 h-full"></div>
              </div>
            </div>
          </div>

          <div class="absolute inset-y-0 right-0 z-10 flex items-center">
            <button
              @click="scrollRight"
              class="bg-white -mr-2 lg:-mr-4 flex justify-center items-center w-10 h-10 rounded-full shadow-lg focus:outline-none z-20"
            >
              <svg
                viewBox="0 0 20 20"
                fill="currentColor"
                class="chevron-right w-6 h-6"
              >
                <path
                  fill-rule="evenodd"
                  d="M7.293 14.707a1 1 0 010-1.414L10.586 10 7.293 6.707a1 1 0 011.414-1.414l4 4a1 1 0 010 1.414l-4 4a1 1 0 01-1.414 0z"
                  clip-rule="evenodd"
                ></path>
              </svg>
            </button>
          </div>
        </div>
      </div>
    </div>
  </layout>
</template>
<style scoped>
.card-header:hover {
  @apply object-contain;
}
.scroll-hider::-webkit-scrollbar {
  scrollbar-track-color: transparent;
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.scroll-hider {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
</style>

<script>
import IntersectionObserver from "../components/IntersectionObserver.vue";
import Layout from "../layouts/Main";
import MedicineCard from "../components/MedicineCard.vue";
import StackedMedicineCard from "../components/MedicalCenter/StackedMedicineCard";
import ImageCard from "../components/MedicalCenter/ImageCard";
import { mapGetters } from "vuex";
import LoadingAnimation from "../components/LoadingAnimation.vue";
import upload from "../components/modal/Upload";
import ImageSearchResult from "../components/modal/ImageSearchResult";

export default {
  name: "Home",
  components: {
    Layout,
    IntersectionObserver,
    MedicineCard,
    LoadingAnimation,
    StackedMedicineCard,
    ImageCard,
    upload,
    ImageSearchResult,
  },
  data() {
    return {
      showLoading: true,
      medName: "",
      scrollPosition: "",
      onUpload: true,
      showSuggestion: false,
      hasValue: false,
      dispatchReqId: "",
      nextRecentLoading: false,
      isLangShow: false,
    };
  },
  computed: {
    languages() {
      return Object.keys(this.$i18n.messages);
    },
    ...mapGetters("recentSearchMod", {
      getRecentMeds: "getRecentMeds",
      getRecentSearch: "getRecentSearch",
      recentData: "recentData",
    }),
    ...mapGetters("searchMedMod", {
      progress: "progress",
    }),
    suggestions() {
      let value = this.$store.getters["suggestion/suggestions"];
      return value;
    },
  },
  watch: {
    onUpload(val) {
      if (val) {
        this.$store.dispatch("searchMedMod/reset");
      }
    },
    medName() {
      this.hasValue = false;
      this.$store
        .dispatch("suggestion/suggestion", this.medName)
        .finally(() => {
          this.$eventbus.$emit("loading-med", false);
        });
    },
    suggestions() {
      if (this.suggestions.length > 0) {
        this.hasValue = true;
      } else {
        this.hasValue = false;
      }
    },
  },
  mounted() {
    // Todo:check success and failier and on success empty and render stng
    this.getRecent();
  },

  methods: {
    nextRecent() {
      console.log(this.recentData);
      if (this.recentData && this.recentData.next_page_url != null) {
        this.nextRecentLoading = true;
        this.$store.dispatch("recentSearchMod/nextRecentMeds").then((res) => {
          console.log(res);
          this.nextRecentLoading = false;
        });
      }
    },
    getRecent() {
      this.showLoading = true;
      // this.$store.dispatch("recentSearchMod/getRecentSearch").finally(() => {
      // this.showLoading = false;
      // });
      this.$store.dispatch("recentSearchMod/getRecentMeds").finally(() => {
        this.showLoading = false;
      });
    },
    modal() {
      this.$eventbus.$emit("show-upload-modal", true);
    },
    startUpload(value) {
      if (value) {
        this.$refs.fileInput.click();
      }
    },
    searchMed() {
      this.$store
        .dispatch("searchMedMod/getMedByName", this.medName)
        .finally(() => {
          this.$eventbus.$emit("search-indicator", false);
          this.$router.push({
            path: "search",
          });
        });
    },

    clickaway() {
      this.showSuggestion = false;
    },
    setSearchInputValue(value) {
      this.medName = value;
      this.hasValue = false;
      this.searchMed();
    },
    onFileChange(e) {
      this.onUpload = false;
      let data = new FormData();
      data.append("image", e.target.files[0]);
      this.$store.dispatch("searchMedMod/searchByImage", data).then((stat) => {
        if (stat && stat.status == 201) {
          this.getRecent();
          this.onUpload = true;
          this.$toast.open("successfully uploaded");
        } else {
          this.$toast.open({
            message: "uploading was not successful",
            type: "error",
          });
        }
      });
    },
    scrollRight() {
      document.querySelector("#parent").scrollLeft =
        document.querySelector("#parent").scrollLeft +
        this.$refs["parent"].clientWidth;
    },
    scrollLeft() {
      document.querySelector("#parent").scrollLeft =
        document.querySelector("#parent").scrollLeft -
        this.$refs["parent"].clientWidth;
    },

    setDesReqId(value) {
      console.log(value);
      this.dispatchReqId = value;
      this.$eventbus.$emit("show-image-search-modal-modal", true);
    },
    setLang(value) {
      localStorage.setItem("lang", value);
      window.location.reload();
    },
    showLang() {
      this.isLangShow = !this.isLangShow;
    },
    clickawayLang() {
      this.isLangShow = false;
    },
  },
};
</script>

<style scoped>
#parent {
  scroll-behavior: smooth;
}
</style>