<template>
  <div>
    <div v-if="loading" class="flex justify-center w-full items-center">
      <loading-animation></loading-animation>
    </div>
    <div class="flex flex-wrap">
      <div v-for="(value, index) in categories" :key="index" class="contents">
        <div v-if="!type">
          <div
            class="flex flex-col text.sm gap-3"
            v-if="value.subcategories && value.subcategories[0] != null"
          >
            <div
              class="text-base font-medium cursor-pointer w-full my-1 py-2 pl-3"
            >
              {{ value.name }}
            </div>

            <div
              @click="searchMedBySubCategory(value2.id)"
              v-for="(value2, index2) in value.subcategories"
              :key="index2"
              class="text-sm pl-5 py-2 cursor-pointer rounded-sm hover:text-white hover:bg-medehanite-primary bg-opacity-90 text-gray-800"
            >
              <span></span>
              {{ value2.name }}
              <!-- jill -->
            </div>
          </div>
        </div>
      </div>
      <div class="flex flex-col" v-if="type">
        <div
          v-for="(value, index) in categories"
          :key="'x' + index"
          class="contents"
        >
          <div
            class="flex flex-col text.sm float-left gap-2"
            v-if="value.subcategories && value.subcategories[0] != null"
          >
            <div
              class="text-base font-medium cursor-pointer hover:bg-gray-100 w-full my-1 py-2 pl-3"
              @click="
                faq_selected !== index
                  ? (faq_selected = index)
                  : (faq_selected = null)
              "
            >
              {{ value.name }}
            </div>

            <div
              class="flex flex-col relative overflow-hidden transition-all max-h-0 duration-700"
              :style="
                faq_selected === index
                  ? `max-height:  ${$el.parentNode.scrollHeight}px`
                  : ``
              "
            >
              <div
                @click="searchMedBySubCategory(value2.id)"
                v-for="(value2, index2) in value.subcategories"
                :key="index2"
                class="text-sm pl-5 py-2 cursor-pointer rounded-sm hover:text-white hover:bg-medehanite-primary bg-opacity-90 text-gray-800"
              >
                <span></span>
                {{ value2.name }}
                <!-- jill -->
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import LoadingAnimation from "./LoadingAnimation.vue";
export default {
  components: { LoadingAnimation },
  data() {
    return {
      faq_selected: null,
      loadingIndicator: true,
      loading: false,
    };
  },
  props: {
    type: {
      type: Boolean,
      default: false,
    },
  },
  watch: {
    categories() {
      if (this.categories.length > 0) {
        this.loading = false;
      }
    },
  },
  mounted() {
    if (
      !(
        this.categories &&
        this.categories[0] != null &&
        this.categories.length > 0
      )
    ) {
      this.$store.dispatch("categoryMod/getCategories").finally(() => {
        this.loading = false;
      });
    }
  },

  methods: {
    searchMedBySubCategory(value) {
      this.$router.push({
        path: "search?show=med",
        query: { medname: value },
      });
    },
  },
  computed: {
    ...mapGetters("categoryMod", {
      categories: "catagories",
    }),
  },
};
</script>