<template>
  <secondary class="overflow-x-hidden" id="parent">
    <!-- <purchase-request-modal></purchase-request-modal> -->
    <div
      v-if="loading"
      class="w-full h-96 relative bg-black bg-opacity-30 flex justify-center items-center"
    >
      <div
        class="flex justify-center rounded-lg bg-white w-20 h-20 items-center"
      >
        <loading-animation></loading-animation>
      </div>
    </div>
    <div class="py-9 bg-medehanite-hero" v-if="!loading">
      <div class="conainer">
        <div class="flex justify-center">
          <div class="mt-2 py-1 pb-5">
            <div class="mx-auto flex-1 flex justify-center">
              <div class="text-2xl font-bold pt-5 pb-2">
                Search Result
                <hr class="w-3/4 mx-auto bg-medehanite-primary h-1 mt-2" />
              </div>
            </div>
          </div>
        </div>
        <div class="w-full-container mb-5" v-if="da.length > 0">
          <div class="overflow-show text-center">
            Medicines that are not found
          </div>
          <div class="flex flex-wrap gap-1 h-full justify-center">
            <div class="h-auto" v-for="(value, index) in da" :key="index">
              <div
                class="w-40 h-16 flex bg-red-300 bg-opacity-80 rounded-md cursor-pointer relative tooltip"
                @click="
                  value.image
                    ? getOrderList
                      ? getOrderList.length
                        ? goto()
                        : voteMed(value.brandName)
                      : voteMed(value.brandName)
                    : voteMed(value.brandName)
                "
              >
                <div class="w-4/12 pr-1 h-full">
                  <img
                    v-if="value.image"
                    :src="storageURL + value.image"
                    class="rounded-md h-full m-auto inset-x-2 object-cover w-full"
                  />
                  <img
                    v-else
                    src="/img/pill.bf7dcf29.svg"
                    class="rounded-md h-full m-auto inset-x-2 object-center w-full"
                  />
                </div>

                <div class="w-8/12 truncate my-auto font-medium">
                  <span>{{ value.brandName }}</span>
                </div>
                <div
                  class="top-0 -mx-20 my-16 absolute w-52 hidden bg-white rounded-md shadow-md px-3 py-2 z-10 tip"
                >
                  <div v-if="!value.image" class="text-gray-500">
                    you can vote for medications that are not found in any of
                    the pharmacies by pressing
                  </div>

                  <div v-else class="text-gray-500">
                    you can order for medications that are not found in any of
                    the pharmacies by pressing
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="flex flex-wrap gap-4 justify-center mt">
          <div class=""></div>

          <div class="flex justify-center w-full">
            <empty
              v-if="!getPharmacy.pharmacies"
              class="w-3/12"
              placeholder=""
            ></empty>
          </div>
          <div v-for="(result, index) in getPharmacy.pharmacies" :key="index">
            <div class="max-w-lg h-40 sm:max-w-xl sm:h-48 sm:mx-9 flex-auto">
              <pharmacy-cardwith-medicine
                :name="result.name"
                :rating="result.rating"
                :longitude="result.longitude"
                :latitude="result.latitude"
                :relativeLocation="result.relativeLocation"
                :medsFound="result.foundMeds"
                :unFoundedMeds="result.unfoundMeds"
                :id="result.id"
                :logo="result.logo"
              ></pharmacy-cardwith-medicine>
            </div>
          </div>
          <span
            ref="orders"
            class="inline-block text-center font-medium w-full text-2xl mt-10"
            v-if="getOrderList && getOrderList.length > 0"
          >
            For your Unfound meds you can order for them here
          </span>
          <div v-for="(result, index) in getOrderList" :key="result.id + index">
            <div class="max-w-lg h-40 sm:max-w-xl sm:h-48 sm:mx-9 flex-auto">
              <pharmacy-cardwith-order-medicine
                :name="result.medical_center.name"
                :rating="result.medical_center.rating"
                :longitude="result.medical_center.longitude"
                :latitude="result.medical_center.latitude"
                :relativeLocation="result.medical_center.relativeLocation"
                :medsFound="result.medical_center.foundMeds"
                :unFoundedMeds="result.medical_center.unfoundMeds"
                :id="result.medical_center.id"
                :logo="result.medical_center.logo"
                :medName="result.medicineName"
                :purchaseId="result.id"
              ></pharmacy-cardwith-order-medicine>
            </div>
          </div>
        </div>
      </div>
    </div>
  </secondary>
</template>
<script>
import { mapGetters } from "vuex";
import Empty from "../../components/Empty.vue";
import LoadingAnimation from "../../components/LoadingAnimation.vue";
import PharmacyCardwithMedicine from "../../components/MedicalCenter/PharmacyCardwithMedicine.vue";
import PharmacyCardwithOrderMedicine from "../../components/MedicalCenter/PharmacyCardwithOrderMedicine.vue";
// import PurchaseRequestModal from "../../components/modal/PurchaseRequest.vue"

import Secondary from "../../layouts/Main.vue";

export default {
  components: {
    PharmacyCardwithMedicine,
    Secondary,
    LoadingAnimation,
    Empty,
    PharmacyCardwithOrderMedicine,
    // PurchaseRequestModal,
  },
  data() {
    return {
      da: [],
      unfoundMedNames: [],
      loading: true,
    };
  },
  name: "fi-pharmacy",
  mounted() {
    // this.$eventbus.$emit("show-purchase-modal", true);

    let parameters = this.$route.query;
    var found = parameters.found.split(",");
    var notFound = parameters.notFound.split(",");

    this.$store
      .dispatch("searchMedicalCenterMod/getPharmacyByMed", {
        found: found,
        notFound: notFound,
      })
      .finally(() => {
        this.da.push(
          ...this.getUnfoundMeds.map(
            (x) =>
              (x = {
                brandName: this.getMeds[x].brandName,
                image: this.getMeds[x].image,
              })
          )
        );

        this.unfoundMedNames.push(
          ...this.getUnfoundMeds.map((x) => (x = this.getMeds[x].brandName))
        );
        this.loading = false;
      });

    this.da = notFound
      .filter((x) => x != "")
      .map((x) => (x = { brandName: x, image: null }));
  },
  methods: {
    storageUrl() {
      return process.env.VUE_APP_STORAGE_BASE_URL;
    },
    voteMed(val) {
      this.$store.dispatch("voteAndOrderMod/vote", val).then((res) => {
        if (res.status == 403) {
          this.$toast.open({
            message: "You have already voted for this medication!!",
            type: "error",
          });
        } else {
          this.$toast.open("successfully voted");
        }
      });
    },
    goto() {
      console.log("here");
      var element = this.$refs["orders"];
      var top = element.offsetTop;
      window.scrollTo(0, top);
    },
  },
  watch: {
    unfoundMedNames(val) {
      if (val.length > 0) {
        this.$store.dispatch("voteAndOrderMod/order", val);
      }
    },
  },
  computed: {
    storageURL() {
      return process.env.VUE_APP_STORAGE_BASE_URL;
    },
    ...mapGetters("searchMedicalCenterMod", {
      getPharmacy: "getPharmaciesWithMed",
      getMeds: "getMeds",
      getUnfoundMeds: "getUnfoundMeds",
    }),
    ...mapGetters("voteAndOrderMod", {
      getOrderList: "getOrderList",
    }),
  },
};
</script>

<style scoped>
.tooltip:hover .tip {
  @apply flex;
}
#parent {
  scroll-behavior: smooth;
}
</style>