<template>
  <authentication-layout>
    <div class="w-full flex justify-center px-8 pb-10 pt-7">
      <img src="../../assets/images/medahaniteLogo.png" alt="" />
    </div>
    <p class="text-center text-2xl font-medium pt-4">
      Enter your mobile Number
    </p>
    <form @submit.prevent="handleSubmit">
      <div class="w-full flex justify-center py-4">
        <div>
          <div class="relative text-gray-600">
            <span class="absolute inset-y-4 left-0 flex items-center pl-1 py-2">
              <div class="py-3 float-left">
                <p class="text-2xl">+251</p>
              </div>
            </span>
            <input
              required
              type="tel"
              name="number"
              maxlength="10"
              :class="[!isValidPhone ? 'border-red-500' : 'border-before' , err ? 'border-red-500' : 'border-before']"
              v-model="phoneNumber"
              placeholder="phone number"
              class="focus:text-gray-700 focus:outline-none pl-16 text-2xl font-normal w-12/12 focus:bg-gray-100 py-2 rounded-md"
            />
          </div>
        </div>
      </div>
      <p class="text-gray-400 text-center">
        when you tap "continue". medehanite will send a text with verification
        codel.The verified phone number can be used to login.
      </p>
      <div class="py-3 flex w-full justify-center">
        <button
          v-if="!loading"
          type="submit"
          class="text-white h-12 w-48 bg-medehanite-primary rounded-full focus:shadow-outline hover:bg-medehanite-focused"
        >
          Continue
        </button>
        <loading-animation v-if="loading"></loading-animation>
      </div>
    </form>
    <p>or</p>
    <div class="pt-3 pb-6 flex w-full justify-center mb-4">
      <button
        type="submit"
        class="text-medehanite-focused h-12 w-48 bg-transparent border-medehanite-primary border rounded-full focus:shadow-outline duration-150 hover:text-white hover:bg-medehanite-primary"
      >
        Sign up
      </button>
    </div>
  </authentication-layout>
</template>
<script>
import LoadingAnimation from "../../components/LoadingAnimation.vue";
import AuthenticationLayout from "../../layouts/Authentication";

// import { mapActions } from "vuex";

export default {
  name: "login",
  components: {
    AuthenticationLayout,
    LoadingAnimation,
  },
  data() {
    return { phoneNumber: "", loading: false ,err:false};
  },
  computed: {
    isValidPhone() {
      return !this.phoneNumber.length || /^0?9\d{8}$/.test(this.phoneNumber);
    },
  },
  methods: {
    handleSubmit() {
      //  console.log('oo');
      if(this.phoneNumber.length >= 9  && this.isValidPhone && this.phoneNumber.charAt(0)=='9'){
        console.log("jack")
        var a = "0" + this.phoneNumber
        console.log(a)
        this.login(a);
      }
     else if (this.phoneNumber.length >= 9 && this.isValidPhone) {
        this.login(this.phoneNumber);
      }
    },
    // ...mapActions("auth", {
    //   login: "login",
    // }),

    async login() {
      this.loading = true;
      try {
        let loginResult = await this.$store
          .dispatch("auth/singUp", this.phoneNumber)
          .then((result) => {
            if (result.success) {
              this.loading = false;
              this.$router.push({
                path: "otp",
              });
            } else {
              this.loading = false;
              this.err = false;
            }
          });
        this.$api.defaults.headers.common["Authorization"] =
          "Bearer " + localStorage.getItem("token");
        console.log(loginResult);
      } catch (e) {
        console.log(e);
        //:Todo vaidate
      }
    },
  },
};
</script>
<style scoped>
.border-before {
  border: solid;
  border-color: rgb(204, 204, 204);
  border-width: thin;
}

input[name="number"] {
  border-width: thin;
}
input[name="number"]:focus {
  border-color: rgba(204, 204, 204, 0.041);
}
</style>
