// import axios from 'axios';
import api from '../../axios'

const state = {
    medicationData: [],
    medications: {},
    selectedMeds: [],
    medSearchName: "",
    progress: 0,
    imageSearch: {}

};
const getters = {
    meds: (state) => state.medications,
    medData: (state) => state.medicationData,
    selectedMeds: (state) => state.selectedMeds,
    indicator: (state) => state.medicationData.length > 0,
    progress: (state) => state.progress,
    imageSearch: (state) => state.imageSearch
};

const actions = {

    async getMedByName({ commit }, value) {
        if (value.length > 0) {
            state.medSearchName = value;
            try {
                const response = await api.get(`medicines?name=${encodeURIComponent(value)}`);
                commit('SET_MED', response.data)
                if (response.data.data.length < 0) {

                    return { 'success': true, 'valid': false };
                }
                return { 'success': true, 'valid': true };
            } catch (err) {
                return { 'success': false };
            }
        } else {
            commit('SET_MED', [])
        }
    },

    async getMedByCategory({ commit }, value) {
        try {

            const response = await api.get(`medicines?subcategoryId=${encodeURIComponent(value.subcategoryId)}`);
            console.log('**************')
            console.log(response);
            console.log('**************')
            commit('SET_MED', response.data)
            return response.data
        } catch (err) {
            return err;
        }

    },
    async searchByImage(states, value) {

        let config = {
            headers: {
                "Content-Type": "multipart/form-data",
            },
            onUploadProgress: progressEvent => {
                var complete = (progressEvent.loaded / progressEvent.total * 100 | 0) + '%'
                state.progress = complete
                console.log(complete);
            }
        };
        try {
            const response = await api.post(`medicines/search/image`, value, config);
            return response;
        } catch (err) {
            return err;
        }
    },

    async imageSearchById({ commit }, value) {
        console.log('amIntheget by id');
        try {
            const response = await api.get(`/dispatchRequest/${value}`);
            commit('IMAGE_SEARCH', response)

            return response;
        } catch (error) {

            return error;
        }

    },

    addMedsToSelected({ commit }, value) {
        console.log(value);
        commit('SET_SELECTED', value)
    },
    removeMedsFromSelected({ commit }, value) {
        commit('REMOVE_SELECTED', value)
    },

    async nextPage({ commit }) {

        if (state.medications.next_page_url != null) {
            try {

                const response = await api.get(`${state.medications.next_page_url}`);
                console.log(state.medications.next_page_url);

                // console.log(`medicines?name=${encodeURIComponent(state.medSearchName)}&page=${encodeURIComponent(state.medications.current_page + 1)}`);
                commit('NEXT_PAGE', response)
                return { 'success': true };
            } catch (err) {
                return { 'success': false };
            }

        }
    },

    reset({ commit }) {
        commit('RESET')
    },

};
const mutations = {
    SET_MED: (state, value) => {
        console.log(value);
        state.medications = value,
            state.medicationData = value.data

    },
    SET_SELECTED: (state, value) => {
        var exists = false;
        for (var i = 0; i < state.selectedMeds.length; i++) {
            if (state.selectedMeds[i].brandName == value.brandName) {
                console.log(state.selectedMeds[i].brandName == value.brandName);
                exists = true;
                break
            }

        }
        if (!exists) {
            state.selectedMeds.push(value)
        }
    },
    IMAGE_SEARCH: (state, value) => (state.imageSearch = value.data),
    REMOVE_SELECTED: (state, value) => (state.selectedMeds = state.selectedMeds.filter(x => x.brandName != value)),
    NEXT_PAGE: (state, value) => {
        state.medications = value.data,
            state.medicationData = state.medicationData.concat(value.data.data)
    },

    RESET: (state) => {
        state.progress = 0
    }

};
export default {
    state,
    getters,
    actions,
    mutations,
    namespaced: true
}