// import axios from 'axios';
import api from '../../axios'

const state = {
    orderRequest: []

};
const getters = {
    getOrderList: (state) => { return state.orderRequest }

};

const actions = {
    async vote({ commit }, value) {
        commit('VOTE')
        try {
            const response = await api.post('/orderRequest',
                {
                    "medicineName": value
                });
            console.log(response.status);
            return response;
        } catch (error) {

            return error;
        }

    },
    async order({ commit }, value) {
        // value = value.map((x) => x = x + '');
        console.log(value)
        try {
            const response = await api.get('/purchaseRequest', {
                params: {
                    "medicineName": ['Entyvio']
                }
            });
            console.log(response.data.data);
            commit('ORDER', response.data.data)
            return response;
        } catch (error) {

            return error;
        }
    },
    async voteForPurchase({ commit }, value) {
        try {
            const response = await api.post(`/purchaseRequest/${value}/vote`,);
            console.log(response);
            commit('VOTE')
            return response;
        } catch (error) {
            return error;
        }
    },

};
const mutations = {
    VOTE: () => { },
    ORDER: (state, value) => {
        state.orderRequest = value;

    },

};
export default {
    state,
    getters,
    actions,
    mutations,
    namespaced: true
}