import api from "../../axios"
const state = {
    catagory : []
}

const getters = {
    catagories: (state) => { return state.catagory }
};

const actions = {
    async getCategories({ commit }) {
        try {
            var response = await api.get('category');
            console.log(response.data)
            commit('CATEGORY', response.data);
        } catch (e) {
            console.log(e)
        }
    }
};

const mutations = {
    CATEGORY: (state, value) => {state.catagory=value},
};
export default {
    state,
    getters,
    actions,
    mutations,
    namespaced : true
}

