<script>
import { mapGetters } from "vuex";
import Category from "../../components/Categories.vue";
// import  SearchBar from "../../components/SearchBar"
export default {
  components: {
    Category,
  },
  data: () => ({
    routes: [
      { name: "Home", path: "/" },
      { name: "Pharmacy", path: "/search?show=phar" },
      { name: "Category", path: "" },
      { name: "Nearby", path: "/nearby" },
      { name: "News", path: "/news" },
      { name: "Search", path: "/search" },
      { name: "About Us", path: "/about" },
    ],
    // userName: "U R",
    showLandingPageNavBar: false,
    showDropDown: false,
    s: {},
    show: true,
    // userNotYet: false,
    faq_selected: false,
  }),
  computed: {
    ...mapGetters("auth", {
      isLoggedIn: "isLoggedIn",
      counter: "counter",
      user: "user",
    }),
    userName() {
      if (this.user.user.firstName == null || this.user.user.lastName == null) {
        return "U R";
      } else {
        return this.user.user.firstName + " " + this.user.user.lastName;
      }
    },
    userNotYet() {
      if (this.user.user.firstName == null || this.user.user.lastName == null) {
        return true;
      } else {
        return false;
      }
    },
  },
  watch: {},
  mounted() {},
  created() {
    if (this.counter < 1) {
      this.$store.dispatch("auth/getUser");
    }
    this.$eventbus.$on("landing-page-intersecting", () => {
      this.showLandingPageNavBar = true;
    });
    this.$eventbus.$on("landing-page-disentagling", () => {
      this.showLandingPageNavBar = false;
    });
  },
  methods: {
    logout() {
      this.$store.dispatch("auth/logout");
    },
    maxHeight() {
      var h = this.$refs.cav;
      console.log(h);
    },
  },
  filters: {
    icon: function (value) {
      if (!value) return "";
      var caps = value[0];
      for (var i = 0; i < value.length; i++) {
        if (value[i] == " ") {
          if (value[i + 1] == " ") continue;
          caps = caps + value[i + 1];
        }
      }
      return caps;
    },
  },
};
</script>
<style scoped>
@media only screen and (max-width: 1027px) {
  .header-sm {
    display: block !important;
  }
}
.chil {
  @apply hidden;
}
.hov-next:hover .chil {
  @apply flex;
}
.chil:hover {
  @apply flex;
}

.cat-par:hover .cat-show {
  @apply flex;
}
.cat-show:hover {
  @apply flex;
}
.cat-show {
  display: none;
}
.cat-par:hover .icon-flip {
  -webkit-transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  transition: all 500ms ease;
  @apply rotate-180;
}
.icon-flip {
  -webkit-transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  transition: all 500ms ease;
  display: block;
}
.menu {
  max-height: 90vh;
}
.max-h-0 {
  max-height: 0;
}
</style>
