const state = {
    location: {}
};
const getters = {
    currentLocation: (state) => { return state.location},
    center:(state) =>{
        if(location.longitude){
        return [state.location.longitude , state.location.latitude]}else return []
    }
};
const actions = {
    setLocation({ commit }, value) {
        console.log(value);
        commit('SET_LOCATION', value)
    }
};

const mutations = {
    SET_LOCATION: (state, value) => (state.location = value),
};
export default {
    state,
    getters,
    actions,
    mutations,
    namespaced: true
}