// import axios from 'axios';
import api from '../../axios'

const state = {
    meds: [],
    recent: {},
    foundMeds: [],
    foundMedObject: {},
    resentMeds: [],
    recentData: {}



};
const getters = {
    getRecentMeds: (state) => state.foundMeds,
    getRecentMedsObject: (state) => (id) => state.foundMedObject[id],

    getRecentSearch: (state) => state.resentMeds,
    getRecentSearchObject: (state) => (id) => {
        return state.resentMeds.find((x) => x.id === id)
    },
    recentData: (state) => {
        return state.recentData
    }
};

const actions = {
    //Todo:need deleteing
    async getRecentSearch({ commit }) {
        try {
            const response = await api.get(`/medicines/recentSearches`);
            commit('SET_RECENT', response.data)
            return { 'success': true, 'valid': true };
        } catch (err) {
            return { 'success': false };
        }
    },
    async getRecentMeds({ commit }) {
        console.log('AM in the recent meds Section')
        state.recentData = {}
        state.resentMeds = []
        try {
            const response = await api.get(`/medicines/recentSearches`);
            commit('SET_RECENT_MEDS', response.data)
            return { 'success': true, 'valid': true };
        } catch (err) {
            return { 'success': false };
        }
    },
    async nextRecentMeds({ commit }) {
        try {
            var response = await api.get(state.recentData.next_page_url);
            commit('SET_RECENT_MEDS', response.data);
            return response.data
        } catch (err) {
            return err
        }
    },
    async getPreImage(ster, value) {
        try {
            const resp = await api.get(`/dispatchRequest/${value}/image`)
            return resp;
        } catch (err) {
            return err
        }
    },


};
const mutations = {
    SET_RECENT: (state, value) => {
        state.recent = value,
            state.meds = value.data
        value.data.map((x) => {
            x.found_meds_obj.map((y) => { state.foundMedObject[y.id] = y })
            state.foundMeds.push(...x.foundMeds)
        });
    },
    SET_RECENT_MEDS: (state, value) => {
        state.recentData = value;
        state.resentMeds.push(...value.data.map((x) => {
            if (x.image != null) {
                x.image = x.image + ""
                console.log(x)
            }
            else if ((x.found_meds_obj.length + x.unfoundMeds.length) > 1 && x.unfoundMeds[0] != null || (x.found_meds_obj.length + x.unfoundMeds.length) > 2) {
                x['stack'] = true
            }
            else {
                x['stack'] = false
            }
            return x;
        }
        ));

    }
};
export default {
    state,
    getters,
    actions,
    mutations,
    namespaced: true
}