<template>
  <transition
    enter-class="opacity-0"
    leave-class="opacity-100"
    enter-to-cass="opacity-100"
    leave-to-class="opacity-0"
    enter-active-class="transition ease-out duration-150"
    leave-active-class="transition ease-out duration-150"
    @after-enter="show = true"
  >
    <div
      v-if="show"
      @click="close"
      class="fixed inset-0 z-50 bg-black bg-opacity-25 grid place-items-center sm:p-6"
    >
      <div class="relative">
        <transition
          enter-class="transform scale-75 opacity-0"
          leave-class="transform scale-100 opacity-100"
          enter-to-cass="transform scale-100 opacity-100"
          leave-to-class="transform scale-75 opacity-0"
          enter-active-class="transition ease-out duration-150"
          leave-active-class="transition ease-out duration-150"
          @after-leave="show = false"
        >
          <div
            class="p-2 pt-9 sm:p-10 sm:pb-6 bg-white w-full h-full max-w-3xl shadow sm:rounded-3xl max-h-screen"
            @click.stop
            ref="modalBox"
          >
            <div
              class="flex w-full h-full justify-center items center"
              v-if="loading"
            >
              <loading-animation></loading-animation>
            </div>
            <!-- <div class="flex flex-col overflow-y-scroll">
<button class="bg-green-400 p-3 rounded-lg" @click="pass">upload</button>
<button class="bg-red-400 p-3 rounded-lg" @click="pass">Cancel</button>
            </div> -->
            <div v-if="!loading">
              <div class="flex-col">
                <div
                  class="w-full px-6 py-3 h-60 flex justify-center items-center"
                  v-if="imageSearch.status != 'Answered'"
                >
                  <span
                    class="text-2xl font-medium text-red-600 bg-red-600 rounded-full bg-opacity-30 px-5 py-3"
                  >
                    {{ imageSearch.status }}
                  </span>
                </div>
                <div class="overflow-y-auto" style="max-height: 83vh">
                  <div
                    class="w-90 p-4"
                    v-for="(result, index) in imageSearch.medicines"
                    :key="index"
                  >
                    <div class="relative w-full mx-auto flex gap-6">
                      <div
                        class="relative rounded-3xl shadow w-80 m-1 result-card hover:shadow-xl"
                      >
                        <!-- <img
                          :src="storageURL + result.image"
                          class="h-48 w-full bg-medehanite-light rounded-t-3xl pt-4 m-auto object-scale-down hover:object-contain card-header"
                        /> -->
                         {{imageSearch.status}}
                        <img
                          :src="storageURL + result.image"
                          class="h-48 w-full bg-medehanite-light rounded-t-3xl pt-4 m-auto object-scale-down hover:object-contain card-header"
                        />
                        <div class="w-full bg-white">
                          <div class="px-3 mx-auto">
                            <div
                              class="w-full text-2xl font-semibold pt-5 pb-2 text-center truncate"
                            >
                           
                              {{ result.brandName }}
                              <hr
                                class="w-1/2 mx-auto bg-medehanite-primary h-1 mt-2"
                              />
                            </div>
                          </div>
                        </div>

                        <div
                          class="bg-white text-base px-3 py-3 rounded-b-3xl text-gray-600"
                        >
                          <div class="text-center">
                            <span class="font-bold pr-1 text-black"
                              >GenericName:</span
                            >

                            <span class="text-left">
                              {{ result.genericName }}</span
                            >
                          </div>
                          <div class="text-center">
                            <span class="font-bold pr-1 text-black"
                              >Manufacturer:</span
                            >
                            {{ result.manufacturer }}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div
                class="mt-4 items-center justify-center space-x-2 gap-12 flex"
              >
                <button
                  class="h-12 w-32 text-medehanite-primary font-medium text-base text-opacity-80 hover:text-opacity-100"
                  @click="cancle"
                >
                  Cancel
                </button>
                <button
                v-if="imageSearch.status == 'Answered'"
                  @click="findInPhar"
                  class="bg-medehanite-light h-12 w-32 text-medehanite-primary font-medium text-base rounded-lg bg-opacity-80 hover:bg-opacity-100"
                >
                  Find in Pharmacy
                </button>
              </div>
            </div>
          </div>
        </transition>
      </div>
    </div>
  </transition>
</template>
<script>
import { mapGetters } from "vuex";
import LoadingAnimation from "../LoadingAnimation.vue";
export default {
  components: { LoadingAnimation },

  data: () => ({
    working: false,
    show: false,
    loading: true,
  }),
  props: ["id"],
  created() {},
  mounted() {
    this.$eventbus.$on("show-image-search-modal-modal", (val) => {
      this.show = val;
    });
  },
  computed: {
    ...mapGetters("searchMedMod", { imageSearch: "imageSearch" }),
    storageURL() {
      return process.env.VUE_APP_STORAGE_BASE_URL;
    },
  },
  methods: {
    getResult() {
      this.$store
        .dispatch("searchMedMod/imageSearchById", this.id)
        .then((res) => {
          if (res && res.status == 200) {
            this.loading = false;
          }
        });
    },
    open() {
      this.show = true;
    },
    close() {
      if (this.working) return;

      this.show = false;
    },
    
    cancle() {
      this.show = false;
    },
    findInPhar() {
      var foundVar = this.imageSearch.medicines
        .map((val) => {
          return val.id;
        })
        .filter((x) => x)
        .toString();
      var notFoundVar = this.imageSearch.unfoundMedicines
        .map((val) => {
          if (!val.valid) {
            return val;
          }
        })
        .filter((x) => x)
        .toString();
        
      this.$router.push({
        name: "fi-pharmacy",
        query: { found: foundVar, notFound: notFoundVar },
      });
    },
  },
  watch: {
    id(val) {
      if (val.length > 7) {
        this.getResult();
      }
    },
  },
};
</script>
<style  scoped>
@media only screen and (max-width: 640px) {
  .heighter {
    height: 83vh !important;
  }
}
.heighter {
  height: 42rem;
}
</style>

