import Vue from 'vue'
import VueI18n from 'vue-i18n'
import en from "./eng/index"
import am from "./amharic/index"

Vue.use(VueI18n);
const messages = {
    English: en,
    Amharic:am,
    
}
export default new VueI18n({
    locale: localStorage.getItem('lang'),
    fallbackLocale: 'English', // set locale
    messages, // set locale messages
   
})